html {
  font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  font-weight: 100;
  height: 100%;
  margin: 0;
}

body {
  height: 100%;
  margin: 0;
  overflow-y: hidden;
  overflow-x: hidden;
  background-color: #fafafa;
  animation: change 3.8s;
}

@keyframes change {
  0% {
    background-color: black;
  }

  90% {
    background-color: black;
  }
  100% {
    background-color: white;
  }
}

h1,
h2,
h4,
h5 {
  margin-top: 0;
  margin-bottom: 0;
  font-weight: 100;
}

img {
  width: 100%;
}

.icon {
  text-decoration: none;
  color: inherit;
  padding: 1rem 1rem;
  transition: all 0.2s;
}

.icon:hover {
  text-decoration: none;
  font-size: 1.1rem;
  cursor: pointer;
}

/*Loading Page*/
.loading-container {
  margin-left: 50px;
  margin-right: 50px;
}

.logo {
  width: 50%;
  display: block;
  margin: 0 auto;
  text-align: center;
  vertical-align: middle;
  z-index: 0;
}

.logo-faded {
  width: 50%;
  display: block;
  margin: 0 auto;
  text-align: center;
  vertical-align: middle;
  z-index: 0;
  opacity: 0;
}

/*Stepper Bar*/

.stepper-main-faded {
  opacity: 1;
  position: absolute;
  left: 0;
  right: 0;
  top: 30%;
  -moz-transition: all 1.5s;
  -webkit-transition: all 1.5s;
  -o-transition: all 1.5s;
  transition: all 1.5s;
  z-index: 1;
}

.stepper-main-fading {
  opacity: 0;
}

.stepper-bar {
  margin: 0 auto;
  position: relative;
  transition-timing-function: linear;
  -webkit-transition: all 3s ease;
  -o-transition: all 3s ease;
  -moz-transition: all 3s ease;
  transition: all 3s ease;

  background: #fafafa;
  display: block;
  height: 3px;
  top: 30px;
  z-index: 1000;
}

#change-completed {
  display: none;
}

/*Navbar animations */

.item {
  position: absolute;
  display: block;
  font-family: 'Hvd fonts brandongrotesque', sans-serif;
  font-size: 14px;
  font-weight: 600;
  letter-spacing: 7px;
  text-decoration: none;
  text-align: center;
  vertical-align: middle;
  z-index: 9999;
  color: #1a1a1a;
  cursor: pointer;
}

button {
  background: none;
  border: none;
  outline-style: none;
}

#waitingTop {
  top: 0;
  transform: translate(0%, -200%);
}

#top {
  top: 50px;
  width: 100%;
  transition: all 1.5s;
}

#waitingRight {
  right: 0;
  transform: translate(200%, 0%);
  width: 10px;
  word-wrap: break-word;
  line-height: 1.3rem;
}

#right {
  height: 95vh;
  right: 50px;
  transition: all 1.5s;
  width: 10px;
  word-wrap: break-word;
  line-height: 1.3rem;
}

#waitingLeft {
  left: 0;
  transform: translate(-200%, 0%);
  width: 10px;
  word-wrap: break-word;
  line-height: 1.3rem;
}

#left {
  top: 40vh;
  left: 50px;
  transition: all 1.5s;
  width: 10px;
  word-wrap: break-word;
  line-height: 1.3rem;
}

#waitingBottom {
  bottom: 0;
  transform: translate(0%, 150%);
}

#bottom {
  bottom: 50px;
  width: 100%;
  transition: all 1.5s;
}

/*HOME STUFF */

.home-fading {
  opacity: 0;
}

.home-faded {
  opacity: 1;
  position: absolute;
  left: 0;
  right: 0;
  top: 30%;
  -moz-transition: all 1.5s;
  -webkit-transition: all 1.5s;
  -o-transition: all 1.5s;
  transition: all 1.5s;
  z-index: 999;
}

.main-description {
  padding-top: 15px;
  padding-left: 50px;
  float: left;
  width: 50%;
  font-size: 20px;
}

.home-description-fading {
  opacity: 0;
}

.home-description-faded {
  opacity: 1;
  -moz-transition: all 1.5s;
  -webkit-transition: all 1.5s;
  -o-transition: all 1.5s;
  transition: all 1.5s;
  text-align: center;
}

.home-logos-fading {
  opacity: 0;
}

.home-logos-faded {
  opacity: 1;
  -moz-transition: all 1.5s;
  -webkit-transition: all 1.5s;
  -o-transition: all 1.5s;
  transition: all 1.5s;
}

.home-logos {
  width: 40%;
  margin: 0 auto;
  margin-top: 3rem;
  padding-left: 5px;
  padding-right: 5px;
  text-align: center;
}

/*ABOUT ME STUFF */

.exp-projects-container-faded {
  opacity: 1;
  position: absolute;
  left: 0;
  right: 0;
  top: 20%;
  width: 70%;
  margin: 0 auto;
  -moz-transition: all 1.5s;
  -webkit-transition: all 1.5s;
  -o-transition: all 1.5s;
  transition: all 1.5s;
  text-align: center;
  overflow-y: scroll;
  overflow-x: hidden;
  height: 420px;
}

.exp-projects-container-fading {
  opacity: 0;
}

.exp-projects-container {
  text-align: center;
  display: block;
}

.exp-projects-item {
  color: rgba(0, 0, 0, 0.87);
  background-color: rgb(255, 255, 255);
  transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
  box-sizing: border-box;
  font-family: Roboto, sans-serif;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 6px, rgba(0, 0, 0, 0.12) 0px 1px 4px;
  border-radius: 2px;
  height: 150px;
  width: 200px;
  margin: 20px;
  text-align: center;
  display: inline-block;
}

.project-image-paper {
  width: 100%;
  max-width: 300px;
  height: 200px;
  margin: 20px;
  text-align: center;
  display: inline-block;
}

.exp-image-paper {
  width: 100%;
  max-width: 450px;
  height: 300px;
  margin: 20px;
  text-align: center;
  display: inline-block;
}

/*IMAGE BLUR */
*,
*:before,
*:after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.bg,
.overlay {
  text-align: center;
}

img,
.overlay {
  transition: 0.3s all;
}

.bg {
  float: left;
  width: 100%;
  height: 100%;
  position: relative;
}

.bg img {
  height: 100%;
  width: 100%;
  margin-bottom: -4px;
}
.bg .overlay {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  background: rgba(0, 0, 0, 0.4);
  opacity: 0;
}
.bg .overlay h2 {
  font-family: 'Hvd fonts brandongrotesque', sans-serif;
  font-size: 14px;
  font-weight: 600;
  padding-top: 30%;
  letter-spacing: 3px;
  color: #ffffff;
}

.bg:hover .overlay {
  opacity: 1;
}
.bg:hover img {
  -webkit-filter: blur(2px);
  filter: blur(2px);
}

@media only screen and (max-width: 768px) {
  .stepper-main-faded {
    top: 35%;
  }

  .home-faded {
    top: 35%;
  }
}
@media only screen and (max-width: 425px) {
  .exp-projects-container-faded {
    width: 100%;
  }

  #right {
    right: 25px;
  }

  #left {
    left: 25px;
  }

  .project-image-paper {
    width: 100%;
    max-width: 200px;
    height: 120px;
  }

  .exp-image-paper {
    width: 100%;
    max-width: 200px;
    height: 120px;
  }

  .item {
    font-size: 10px;
  }

  .item:hover {
    font-size: 10px;
  }

  .home-logos {
    width: 30%;
  }
}

@media only screen and (max-width: 375px) {
  .logo {
    width: 65%;
  }

  .logo-faded {
    width: 65%;
  }

  .home-logos {
    width: 40%;
  }
}

@media only screen and (max-height: 730px) {
  .exp-projects-container-faded {
    height: 400px;
  }
}
